.waiting-room {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    position: relative;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    // background-image: url("../../public/testris.jpg"); /* 부모에 배경 이미지 적용 */
    overflow: hidden; /* 내부 요소가 바깥으로 넘치지 않도록 설정 */
    z-index: 1000; /* 우선순위를 높게 설정하여 테트리스 블록 위에 위치하도록 */
}

.waiting-room > * {
    background-color: white; /* 자식 요소의 배경색을 흰색으로 설정 */
    background-color: rgba(255, 255, 255, 0.85);
}

.logo-and-menubar {
    display: flex;
    justify-content: space-between; /* 로고와 메뉴바를 양쪽 끝으로 배치 */
    align-items: center;
    width: 65%; /* 전체 너비의 90% 사용 */
    max-width: 1024px; /* 최대 너비 설정 */
    margin: 0 auto; /* 상하 마진 없이 좌우 마진으로 중앙 정렬 */
    padding: 20px; /* 패딩 조정 */
}

