.ListTitle {
    display: flex;
    align-items: center;
    justify-content: flex-start; /* 왼쪽 정렬 */

    padding: 10px 20px; /* 상단과 하단 패딩 */
    width: 100%; /* 전체 너비 */
    max-width: 780px; /* 최대 너비 설정 */
    margin: 20px auto; /* 위아래 마진 지정, 좌우는 자동으로 중앙 정렬 */
    // border-radius: 10px;
    border-bottom: 2px solid #333;
}

.roomListTitle {
    font-size: 1.5rem; /* 글자 크기 */
    font-weight: bold; /* 글자를 굵게 */
    margin-right: auto; /* 오른쪽 여백을 자동으로 설정하여 버튼을 오른쪽 끝으로 밀어냄 */
}

.btnRoom {
    padding: 10px 15px;
    margin: 10px; /* 여백 조정 */
    width: 120px; /* 버튼 너비 */
    font-weight: 600;
    text-align: center;
    line-height: 50px;
    border-radius: 10px;
    transition: all 0.2s;
    cursor: pointer; /* 마우스 오버 시 포인터로 변경 */
    background-color: #f1f1f1; /* 배경색 추가 */
    border: none; /* 테두리 없앰 */
}

.btnJoin {
    background-color: aliceblue;
    z-index: 0px;
}

.btnJoin.btnPush {
    box-shadow: 0px 1px 0px 0px rgb(0, 0, 0);
}

.btnJoin:hover {
    margin-top: 15px;
    margin-bottom: 5px;
}

/* RoomList.jsx */
.RoomList {
    width: 100%; /* 부모 컨테이너에 맞게 너비 설정 */
    max-width: 780px; /* 최대 너비 설정 */
    margin: 20px auto; /* 상하 여백 지정, 좌우는 자동으로 중앙 정렬 */
    padding: 20px; /* 패딩 설정 */
}

.ShowRoomList {
    margin-bottom: 10px; /* 섹션 사이의 여백 */
}

.ListRoom ul {
    list-style-type: none; /* 기본 목록 스타일 제거 */
    padding: 0; /* 기본 패딩 제거 */
}

.ListRoom li {
    border: 1px solid #333; /* 테두리 */
    border-radius: 5px; /* 모서리 둥글게 */
    margin-bottom: 10px; /* 요소 사이의 마진 */
    padding: 15px; /* 내부 여백 */
    display: flex;
    align-items: center;
    justify-content: space-between; /* 요소 내부 컨텐츠를 양쪽 끝으로 정렬 */
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1); /* 그림자 효과 */
    transition: background-color 0.3s; /* 호버 효과를 위한 전환 */
}

//방 제목
.ListRoom span {
    font-size: 1.2rem; /* 방 제목의 폰트 사이즈 */
    color: #333; /* 폰트 색상 */
}
//입장하기 버튼
.ListRoom button {
    padding: 10px 20px; /* 버튼 패딩 */
    background-color: white; /* 버튼 배경색 */
    box-shadow: 0 2px 3px 0 rgba(34, 36, 38, 0.15);
    color: #333; /* 버튼 폰트 색상 */
    border: 2px solid #333;
    border-radius: 10px;
    border-bottom: 2px solid #333;
    transition: background-color 0.3s, color 0.3s, border-color 0.3s;
}
.ListRoom button:hover {
    background-color: #3498db; /* 호버 시 배경색 변경 */
    color: white; /* 호버 시 글자 색상 변경 */
    border-color: #2980b9; /* 호버 시 테두리 색상 변경 */
}
/* [ 방만들기  모달, 공통 모달 적용 X ] */
.modal-outside {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    justify-content: center;
    align-items: center;
}

.CreateRoom {
    background-color: white;
    padding: 40px;
    width: 90%;
    max-width: 500px;
    border-radius: 10px;
    box-shadow: 0 2px 3px 0 rgba(34, 36, 38, 0.15);
}

@media (max-width: 768px) {
    .logo-and-menubar {
        width: 95%;
    }

    .ListTitle,
    .RoomList {
        max-width: 100%; /* 모바일에서 최대 너비를 100%로 조정 */
        padding: 10px; /* 모바일에서 패딩 감소 */
    }

    .ListRoom li {
        width: 90%;
    }

    .btnRoom {
        width: 15%; /* 모바일에서 버튼 너비를 100%로 조정 */
        margin-right: 5%;
    }
}

@media (min-width: 769px) and (max-width: 1024px) {
    /* 태블릿 크기 조정 */
    .logo-and-menubar {
        padding: 10px; /* 태블릿에서 패딩 조정 */
    }
}

@media (min-width: 1025px) {
    /* 데스크탑 크기 조정 */
    .logo-and-menubar,
    .ListTitle,
    .RoomList {
        max-width: 1024px; /* 데스크탑에서 최대 너비 설정 */
    }
}
@keyframes fall {
    from {
        transform: translateY(-30px);
    }
    to {
        transform: translateY(calc(100vh - 80px));
    }
}

.tetris-block {
    position: absolute;
    top: 0;
    left: 0;
    display: grid;
    grid-template-columns: repeat(4, 30px);
    gap: 2px;
    z-index: -1000; /* 부모 요소에 의해 가려지도록 */
    animation: fall linear forwards;
}

.tetris-square {
    width: 30px;
    height: 30px;
}
